import React from 'react'

import SummaryContent from './SummaryContent'

export default class Summary extends React.Component {
    render() {
        return (
            <section id="summary-section" className="section-container">
                <div id="summary-container">
                    <div className="heading-container">
                        <h2 className="subheading">About Me</h2>
                        <h2 className="heading">Summary</h2>
                    </div>

                    <div id="summary-content">
                        <div className="timeline">
                            <SummaryContent
                                company="I am an undergraduate in the CS Department at National Chiao Tung University."
                                company2="Currently a wireless technical intern at Intel since July 2020."
                                company3="Passionate about network programming, especially wireless network."
                                company4="Also actively try different development boards and IoT environments."
                            />
                            
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
